nav .container-fluid {
    display: flex;
}

.navbar {
    background-color: white;
    color: black;
    padding: 0;
    box-shadow: var(--shadow);
}

.navColorBlock {
    height: 62px;
    background-color: var(--yellow);
    width: 40px;
    margin-right: 1rem;
}

.navbar-brand {
    font-family: "Arvo", sans-serif;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all .2s ease-in;
}

.navbar-brand:hover {
    font-weight: 700;
}

nav form {
    display: flex;
}

.navbar-nav {
    padding-right: 40px;
}

nav .form-select {
    width: 10rem;
}

nav .form-select,
nav .form-control,
nav .btn {
    border-radius: 0;
}

nav .btn {
    padding: 8px !important;
}

nav button {
    border: none !important;
}

.avatarContainer {
    cursor: pointer;
}

.avatarContainer span {
    display: none;
}

.avatar {
    border-radius: 50%;
    background-color: var(--yellow);
    width: 3rem;
    height: 3rem;
}

.avatar h1 {
    font-size: 1.5rem;
}

@media screen and (max-width:768px) {
    .navbar-nav {
        padding: 0;
    }
    .searchBar {
        padding-bottom: 1rem;
    }
    .avatarContainer {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .avatarContainer span {
        display: block;
        padding-bottom: 1rem;
        width: 100%;
    }
    .avatar {
        display: none !important;
    }
}