.hostComment {
    background-color: var(--lightGrey);
}

.comment,
.comment button {
    font-size: 16px;
    font-weight: 700;
}

.comment h5 {
    font-size: 14px;
    color: var(--purple);
    font-weight: 700;
}

.comment p {
    line-height: 1.5rem;
}

.comment button {
    border: none;
    color: var(--navy);
    background-color: transparent;
    margin-bottom: 1rem;
}

.comment button:hover {
    color: var(--btnHover);
}

.replyContainer button.position-absolute {
    right: 0;
}