@import url(https://fonts.googleapis.com/css2?family=Arvo&family=Lato:wght@400;700&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --navy: #485696;
    --yellow: #ffd275;
    --purple: #706993;
    --medGrey: #D6D6D6;
    --lightGrey: #F6F6F6;
    --btnHover: #4761d4;
    --shadow: 0px 2px 12px 3px #ebebeb;
}

img {
    width: 100%;
    height: 100%;
}

body {
    font-family: 'Lato', sans-serif;
    background: #FFF;
}

footer {
    background-color: #ffd275;
    background-color: var(--yellow);
    width: 100%;
    display: block;
    height: 6rem;
}

h1 {
    font-family: 'Arvo', cursive;
    font-size: clamp(1.5rem, 3.5vw, 4rem);
}

h3 {
    font-size: clamp(1rem, 2.3vw, 3rem);
}


/*Button*/

.btn {
    background-color: #485696;
    background-color: var(--navy);
    color: white;
    font-family: 'Lato', sans-serif;
    font-weight: 700;
    font-size: 1rem;
    padding: 2px !important;
}

.btn:hover {
    background-color: #4761d4;
    background-color: var(--btnHover);
    color: white;
}
#auth {
    height: 100vh;
    width: 100vw;
}

.auth {
    width: 100%;
    padding-right: 7rem;
}

.authImg {
    width: 400px;
}

.authImg img {
    z-index: -1;
    position: relative;
    width: 100%;
    height: 100%;
    left: 7rem;
}

.authForm {
    max-width: 400px;
    box-shadow: 1px 2px 12px 3px #f5f5f5;
    padding: 2rem;
    background: white;
}

.formBackBtn {
    opacity: .3;
    cursor: pointer;
    transition: all .3s ease-in;
}

.formBackBtn:hover {
    opacity: 1;
    color: var(--purple);
    font-weight: 700;
}

.authForm h1 {
    font-size: 2.5rem;
}

.authForm input {
    border-style: none none solid none;
    border-width: 2px;
    border-color: var(--medGrey);
    padding: 3px;
    margin-bottom: 1rem;
}

.authForm input::-webkit-input-placeholder {
    color: var(--medGrey);
}

.authForm input:-ms-input-placeholder {
    color: var(--medGrey);
}

.authForm input::placeholder {
    color: var(--medGrey);
}

.authOptions {
    padding-top: 1rem;
    width: 100%;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.authOptions .btn {
    width: 12rem;
    margin-right: 1rem;
}

.authOptions button:nth-child(2) {
    border: none;
    color: var(--navy);
    font-weight: 700;
    text-decoration: underline;
    background-color: white;
}

.authOptions button:nth-child(2):hover {
    color: var(--btnHover);
    font-size: 1.1rem;
}

@media screen and (max-width: 576px) {
    .authImg {
        display: none;
    }
    .auth {
        padding-right: 0rem;
    }
}
nav .container-fluid {
    display: -webkit-flex;
    display: flex;
}

.navbar {
    background-color: white;
    color: black;
    padding: 0;
    box-shadow: var(--shadow);
}

.navColorBlock {
    height: 62px;
    background-color: var(--yellow);
    width: 40px;
    margin-right: 1rem;
}

.navbar-brand {
    font-family: "Arvo", sans-serif;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all .2s ease-in;
}

.navbar-brand:hover {
    font-weight: 700;
}

nav form {
    display: -webkit-flex;
    display: flex;
}

.navbar-nav {
    padding-right: 40px;
}

nav .form-select {
    width: 10rem;
}

nav .form-select,
nav .form-control,
nav .btn {
    border-radius: 0;
}

nav .btn {
    padding: 8px !important;
}

nav button {
    border: none !important;
}

.avatarContainer {
    cursor: pointer;
}

.avatarContainer span {
    display: none;
}

.avatar {
    border-radius: 50%;
    background-color: var(--yellow);
    width: 3rem;
    height: 3rem;
}

.avatar h1 {
    font-size: 1.5rem;
}

@media screen and (max-width:768px) {
    .navbar-nav {
        padding: 0;
    }
    .searchBar {
        padding-bottom: 1rem;
    }
    .avatarContainer {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: center;
                justify-content: center;
    }
    .avatarContainer span {
        display: block;
        padding-bottom: 1rem;
        width: 100%;
    }
    .avatar {
        display: none !important;
    }
}
#header {
    background-color: var(--lightGrey);
}

.headerImg img:nth-child(2) {
    display: inline-block;
    z-index: 2;
    left: -20%;
    height: 100% !important;
}

.headerDets .btn {
    max-width: 50%;
    min-width: 15rem;
    font-size: 1rem;
    white-space: nowrap;
}

.headerDets h3 {
    color: var(--purple);
}

.headerExplore h3 {
    color: black;
}

.cat-btn {
    background-color: var(--yellow);
    color: black;
    font-size: 1rem;
    padding: 0 1rem !important;
    margin: 0 .5rem .5rem 0;
}

.cat-btn:hover {
    background-color: #ffb522;
    color: black;
}

@media screen and (max-width:768px) {
    .headerImg {
        -webkit-justify-content: space-between !important;
                justify-content: space-between !important;
        width: 100% !important;
    }
    .headerImg img:nth-child(2) {
        z-index: 2;
        left: -36%;
        width: 56% !important;
    }
    .headerDets,
    .headerExplore {
        text-align: center;
    }
}
.forumFeature {
    margin: 2rem;
    padding: 2rem;
}

.forumFeature .slick-prev:before,
.forumFeature .slick-next:before {
    color: var(--medGrey);
}
.forumContainer {
    height: auto;
    margin: 1rem 0;
    display: inline-block;
    /* width: calc(2* clamp(8rem, 12vw, 12rem)); */
}

.forumCover {
    background-size: 100% 100%;
    z-index: 2;
}

.expandForum {
    -webkit-filter: blur(2px);
    -moz-filter: blur(2px);
    -o-filter: blur(2px);
    -ms-filter: blur(2px);
    filter: blur(2px);
    z-index: -1;
}

.expandForum::after {
    content: '';
    width: clamp(8rem, 12vw, 12rem);
    height: clamp(11rem, 16vw, 16rem);
    background-color: rgba(255, 209, 117, 0.788);
    border-radius: .3rem;
}

.forumContainer .btn {
    z-index: 2;
    width: calc(.7* clamp(8rem, 12vw, 12rem));
    top: 25%;
    margin: 0 1.4rem;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
}

.forum .forumCover,
.forumPeak {
    width: clamp(8rem, 12vw, 12rem);
    height: clamp(11rem, 16vw, 16rem);
}

.forumPeak {
    background-color: white;
    color: black;
    padding: .5rem;
    border-width: 2px;
    border-color: var(--medGrey);
    border-style: solid solid solid none;
    border-radius: 0 .3rem .3rem 0;
    font-size: 14px;
    position: relative;
    z-index: 3;
}

.forumTitle {
    width: clamp(8rem, 12vw, 12rem);
    word-wrap: break-word;
    font-size: 12px;
}

.forumHost {
    color: var(--purple);
    font-weight: 700;
    font-size: 12px;
    width: clamp(8rem, 12vw, 12rem);
    word-wrap: break-word;
}

.forumContainer h5 {
    display: inline-block;
    font-size: 14px;
    font-weight: 700;
    padding: 0;
    margin: 0;
    width: clamp(8rem, 12vw, 12rem);
    word-wrap: break-word;
}
.modalAccent {
    width: 2rem;
    height: 5rem;
    background-color: var(--yellow);
    left: 0rem;
    top: 0rem;
}

.modal-body p {
    width: 75%;
}

.returnBtn {
    border: none;
    background-color: transparent;
    color: var(--medGrey);
    font-weight: bold;
    transition: all .3s ease-in;
}

.returnBtn:hover {
    color: black;
}

.forumComment {
    resize: none;
}

.modalContainer {
    background-color: white;
}

.loginOps button:nth-child(2) {
    border: none;
    background-color: transparent;
    color: var(--navy);
    font-weight: bold;
}

.discourse {
    height: calc(100vh - 62px);
}

.discourseSidebar {
    box-shadow: var(--shadow);
    height: auto;
}

.discourseSidebar p:first-child {
    color: var(--purple);
}

.discourseSidebar img {
    height: 60%;
    max-width: 15rem;
    max-height: 20rem;
}

.discourseSidebar button {
    max-width: 15rem;
}

.discourseMain {
    height: auto;
}

@media screen and (max-width: 480px) {
    .modal-body p {
        width: 100%;
    }
}
.hostComment {
    background-color: var(--lightGrey);
}

.comment,
.comment button {
    font-size: 16px;
    font-weight: 700;
}

.comment h5 {
    font-size: 14px;
    color: var(--purple);
    font-weight: 700;
}

.comment p {
    line-height: 1.5rem;
}

.comment button {
    border: none;
    color: var(--navy);
    background-color: transparent;
    margin-bottom: 1rem;
}

.comment button:hover {
    color: var(--btnHover);
}

.replyContainer button.position-absolute {
    right: 0;
}
/*Edit Profile Modal */

.profilePage .nav-link.active {
    color: var(--purple);
    font-weight: 700;
}

.modal-header {
    border-bottom: 0 none;
}

.modal-footer {
    border-top: 0 none;
}

.modal input {
    font-family: 'Lato', sans-serif;
}

#image input {
    border-style: none none solid none;
    border-width: 2px;
    border-color: var(--medGrey);
    padding: 3px;
    margin-bottom: 1rem;
}

#image input::-webkit-input-placeholder {
    color: var(--medGrey);
}

#image input:-ms-input-placeholder {
    color: var(--medGrey);
}

#image input::placeholder {
    color: var(--medGrey);
}

#profile input,
#delete input {
    border-style: none none solid none;
    border-width: 2px;
    border-color: var(--medGrey);
    padding: 3px;
    margin-bottom: 1rem;
}

.profile {
    height: calc(100vh - 62px);
}

.profileMain,
.profileSidebar {
    height: auto;
}


/*SIDEBAR*/

.profileSidebar {
    box-shadow: var(--shadow);
}

.profileSidebar img {
    height: 10rem;
    width: 10rem;
}

.profileSidebar .imgEdit {
    all: unset;
    font-size: 14px;
    color: var(--navy);
    text-decoration: underline;
    font-family: 'Lato', sans-serif;
    cursor: pointer;
    transition: all .3s ease-in;
}

.profileSidebar .imgEdit:hover {
    font-weight: 700;
}

.profileSidebar .btn {
    max-width: 10rem;
}

.profileSidebar h1 {
    font-size: 1.5rem;
}

.profileSidebar .delAcctBtn {
    color: var(--navy);
    background-color: transparent;
    border: 1px solid var(--navy);
    transition: all .3s ease-in;
}

.profileSidebar .delAcctBtn:hover {
    background-color: rgba(255, 0, 21, 0.726);
    color: black;
    border: none;
}

.profileSidebar .editBtn {
    background-color: var(--yellow);
    color: black;
}

.profileSidebar .editBtn:hover {
    transition: all .3s ease-in;
    background-color: #ffc552;
}


/*MAIN*/

.profileMainContent h1 {
    font-size: clamp(1.5rem, 3.5vw, 2rem);
}

.userForums {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.userForum,
.membership {
    width: 7rem;
    -webkit-flex: 0 0 7rem;
            flex: 0 0 7rem;
    margin: 1rem 1rem 0rem 0;
}

.userForum img,
.recentActivity img {
    object-fit: cover;
    height: 10rem;
}

.bookClubBtns button {
    width: 6rem;
    margin-left: .5rem;
}

.bookClubBtns button:nth-child(2) {
    color: var(--navy);
    background-color: white;
    border: 1px solid var(--navy);
    transition: all .3s ease-in;
}

.bookClubBtns button:nth-child(2):hover {
    background-color: rgba(255, 0, 21, 0.726);
    color: black;
    border: none;
}

.userForum h6 {
    font-weight: 700;
    font-size: 14px;
    color: black;
}

.userForum p {
    color: black;
    font-size: 12px;
}

.activity {
    background-color: white;
    color: black;
    box-shadow: var(--shadow);
}

.activity img {
    width: 7rem;
    margin-right: 1rem;
    position: relative;
    z-index: 2;
}

.activity .btn {
    width: 6rem;
    margin-left: .5rem;
}

.activityHover {
    -webkit-filter: blur(2px);
    -moz-filter: blur(2px);
    -o-filter: blur(2px);
    -ms-filter: blur(2px);
    filter: blur(2px);
}

.coverOver {
    background-color: rgba(255, 209, 117, 0.671);
    width: 7rem;
    height: 10rem;
    border-radius: .3rem;
    top: 0;
    left: 0;
}

.imgContainer {
    height: auto;
}


/*MEDIA*/

@media screen and (min-width:768px) {
    .col-md-9 {
        -webkit-flex: 1 0 auto;
                flex: 1 0 auto;
    }
}
.resultsContent h1 {
    margin-top: 1rem;
}

.results {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: auto;
}
