/*Edit Profile Modal */

.profilePage .nav-link.active {
    color: var(--purple);
    font-weight: 700;
}

.modal-header {
    border-bottom: 0 none;
}

.modal-footer {
    border-top: 0 none;
}

.modal input {
    font-family: 'Lato', sans-serif;
}

#image input {
    border-style: none none solid none;
    border-width: 2px;
    border-color: var(--medGrey);
    padding: 3px;
    margin-bottom: 1rem;
}

#image input::placeholder {
    color: var(--medGrey);
}

#profile input,
#delete input {
    border-style: none none solid none;
    border-width: 2px;
    border-color: var(--medGrey);
    padding: 3px;
    margin-bottom: 1rem;
}

.profile {
    height: calc(100vh - 62px);
}

.profileMain,
.profileSidebar {
    height: auto;
}


/*SIDEBAR*/

.profileSidebar {
    box-shadow: var(--shadow);
}

.profileSidebar img {
    height: 10rem;
    width: 10rem;
}

.profileSidebar .imgEdit {
    all: unset;
    font-size: 14px;
    color: var(--navy);
    text-decoration: underline;
    font-family: 'Lato', sans-serif;
    cursor: pointer;
    transition: all .3s ease-in;
}

.profileSidebar .imgEdit:hover {
    font-weight: 700;
}

.profileSidebar .btn {
    max-width: 10rem;
}

.profileSidebar h1 {
    font-size: 1.5rem;
}

.profileSidebar .delAcctBtn {
    color: var(--navy);
    background-color: transparent;
    border: 1px solid var(--navy);
    transition: all .3s ease-in;
}

.profileSidebar .delAcctBtn:hover {
    background-color: rgba(255, 0, 21, 0.726);
    color: black;
    border: none;
}

.profileSidebar .editBtn {
    background-color: var(--yellow);
    color: black;
}

.profileSidebar .editBtn:hover {
    transition: all .3s ease-in;
    background-color: #ffc552;
}


/*MAIN*/

.profileMainContent h1 {
    font-size: clamp(1.5rem, 3.5vw, 2rem);
}

.userForums {
    flex-wrap: wrap;
}

.userForum,
.membership {
    width: 7rem;
    flex: 0 0 7rem;
    margin: 1rem 1rem 0rem 0;
}

.userForum img,
.recentActivity img {
    object-fit: cover;
    height: 10rem;
}

.bookClubBtns button {
    width: 6rem;
    margin-left: .5rem;
}

.bookClubBtns button:nth-child(2) {
    color: var(--navy);
    background-color: white;
    border: 1px solid var(--navy);
    transition: all .3s ease-in;
}

.bookClubBtns button:nth-child(2):hover {
    background-color: rgba(255, 0, 21, 0.726);
    color: black;
    border: none;
}

.userForum h6 {
    font-weight: 700;
    font-size: 14px;
    color: black;
}

.userForum p {
    color: black;
    font-size: 12px;
}

.activity {
    background-color: white;
    color: black;
    box-shadow: var(--shadow);
}

.activity img {
    width: 7rem;
    margin-right: 1rem;
    position: relative;
    z-index: 2;
}

.activity .btn {
    width: 6rem;
    margin-left: .5rem;
}

.activityHover {
    -webkit-filter: blur(2px);
    -moz-filter: blur(2px);
    -o-filter: blur(2px);
    -ms-filter: blur(2px);
    filter: blur(2px);
}

.coverOver {
    background-color: rgba(255, 209, 117, 0.671);
    width: 7rem;
    height: 10rem;
    border-radius: .3rem;
    top: 0;
    left: 0;
}

.imgContainer {
    height: auto;
}


/*MEDIA*/

@media screen and (min-width:768px) {
    .col-md-9 {
        flex: 1 0 auto;
    }
}