#auth {
    height: 100vh;
    width: 100vw;
}

.auth {
    width: 100%;
    padding-right: 7rem;
}

.authImg {
    width: 400px;
}

.authImg img {
    z-index: -1;
    position: relative;
    width: 100%;
    height: 100%;
    left: 7rem;
}

.authForm {
    max-width: 400px;
    box-shadow: 1px 2px 12px 3px #f5f5f5;
    padding: 2rem;
    background: white;
}

.formBackBtn {
    opacity: .3;
    cursor: pointer;
    transition: all .3s ease-in;
}

.formBackBtn:hover {
    opacity: 1;
    color: var(--purple);
    font-weight: 700;
}

.authForm h1 {
    font-size: 2.5rem;
}

.authForm input {
    border-style: none none solid none;
    border-width: 2px;
    border-color: var(--medGrey);
    padding: 3px;
    margin-bottom: 1rem;
}

.authForm input::placeholder {
    color: var(--medGrey);
}

.authOptions {
    padding-top: 1rem;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
}

.authOptions .btn {
    width: 12rem;
    margin-right: 1rem;
}

.authOptions button:nth-child(2) {
    border: none;
    color: var(--navy);
    font-weight: 700;
    text-decoration: underline;
    background-color: white;
}

.authOptions button:nth-child(2):hover {
    color: var(--btnHover);
    font-size: 1.1rem;
}

@media screen and (max-width: 576px) {
    .authImg {
        display: none;
    }
    .auth {
        padding-right: 0rem;
    }
}