@import url('https://fonts.googleapis.com/css2?family=Arvo&family=Lato:wght@400;700&display=swap');
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    --navy: #485696;
    --yellow: #ffd275;
    --purple: #706993;
    --medGrey: #D6D6D6;
    --lightGrey: #F6F6F6;
    --btnHover: #4761d4;
    --shadow: 0px 2px 12px 3px #ebebeb;
}

img {
    width: 100%;
    height: 100%;
}

body {
    font-family: 'Lato', sans-serif;
    background: #FFF;
}

footer {
    background-color: var(--yellow);
    width: 100%;
    display: block;
    height: 6rem;
}

h1 {
    font-family: 'Arvo', cursive;
    font-size: clamp(1.5rem, 3.5vw, 4rem);
}

h3 {
    font-size: clamp(1rem, 2.3vw, 3rem);
}


/*Button*/

.btn {
    background-color: var(--navy);
    color: white;
    font-family: 'Lato', sans-serif;
    font-weight: 700;
    font-size: 1rem;
    padding: 2px !important;
}

.btn:hover {
    background-color: var(--btnHover);
    color: white;
}