.modalAccent {
    width: 2rem;
    height: 5rem;
    background-color: var(--yellow);
    left: 0rem;
    top: 0rem;
}

.modal-body p {
    width: 75%;
}

.returnBtn {
    border: none;
    background-color: transparent;
    color: var(--medGrey);
    font-weight: bold;
    transition: all .3s ease-in;
}

.returnBtn:hover {
    color: black;
}

.forumComment {
    resize: none;
}

.modalContainer {
    background-color: white;
}

.loginOps button:nth-child(2) {
    border: none;
    background-color: transparent;
    color: var(--navy);
    font-weight: bold;
}

.discourse {
    height: calc(100vh - 62px);
}

.discourseSidebar {
    box-shadow: var(--shadow);
    height: auto;
}

.discourseSidebar p:first-child {
    color: var(--purple);
}

.discourseSidebar img {
    height: 60%;
    max-width: 15rem;
    max-height: 20rem;
}

.discourseSidebar button {
    max-width: 15rem;
}

.discourseMain {
    height: auto;
}

@media screen and (max-width: 480px) {
    .modal-body p {
        width: 100%;
    }
}