.forumContainer {
    height: auto;
    margin: 1rem 0;
    display: inline-block;
    /* width: calc(2* clamp(8rem, 12vw, 12rem)); */
}

.forumCover {
    background-size: 100% 100%;
    z-index: 2;
}

.expandForum {
    -webkit-filter: blur(2px);
    -moz-filter: blur(2px);
    -o-filter: blur(2px);
    -ms-filter: blur(2px);
    filter: blur(2px);
    z-index: -1;
}

.expandForum::after {
    content: '';
    width: clamp(8rem, 12vw, 12rem);
    height: clamp(11rem, 16vw, 16rem);
    background-color: rgba(255, 209, 117, 0.788);
    border-radius: .3rem;
}

.forumContainer .btn {
    z-index: 2;
    width: calc(.7* clamp(8rem, 12vw, 12rem));
    top: 25%;
    margin: 0 1.4rem;
    backdrop-filter: blur(10px);
}

.forum .forumCover,
.forumPeak {
    width: clamp(8rem, 12vw, 12rem);
    height: clamp(11rem, 16vw, 16rem);
}

.forumPeak {
    background-color: white;
    color: black;
    padding: .5rem;
    border-width: 2px;
    border-color: var(--medGrey);
    border-style: solid solid solid none;
    border-radius: 0 .3rem .3rem 0;
    font-size: 14px;
    position: relative;
    z-index: 3;
}

.forumTitle {
    width: clamp(8rem, 12vw, 12rem);
    word-wrap: break-word;
    font-size: 12px;
}

.forumHost {
    color: var(--purple);
    font-weight: 700;
    font-size: 12px;
    width: clamp(8rem, 12vw, 12rem);
    word-wrap: break-word;
}

.forumContainer h5 {
    display: inline-block;
    font-size: 14px;
    font-weight: 700;
    padding: 0;
    margin: 0;
    width: clamp(8rem, 12vw, 12rem);
    word-wrap: break-word;
}