#header {
    background-color: var(--lightGrey);
}

.headerImg img:nth-child(2) {
    display: inline-block;
    z-index: 2;
    left: -20%;
    height: 100% !important;
}

.headerDets .btn {
    max-width: 50%;
    min-width: 15rem;
    font-size: 1rem;
    white-space: nowrap;
}

.headerDets h3 {
    color: var(--purple);
}

.headerExplore h3 {
    color: black;
}

.cat-btn {
    background-color: var(--yellow);
    color: black;
    font-size: 1rem;
    padding: 0 1rem !important;
    margin: 0 .5rem .5rem 0;
}

.cat-btn:hover {
    background-color: #ffb522;
    color: black;
}

@media screen and (max-width:768px) {
    .headerImg {
        justify-content: space-between !important;
        width: 100% !important;
    }
    .headerImg img:nth-child(2) {
        z-index: 2;
        left: -36%;
        width: 56% !important;
    }
    .headerDets,
    .headerExplore {
        text-align: center;
    }
}